.beer-card {
    background: rgb(112,90,238);
    width: 90%;
    height: 20rem;
    border-radius: 10px;

} 

h2{
    font-size: medium;
}

.beer__tag{
    font-style: italic;
    text-decoration: none;
}

.beer__image{
        max-width: 100%;
        height: 70%;
        opacity: 0.6;
    }

.beer__info{
        display: flex;
        justify-content: space-around;
        opacity: 1;
    }

.beer-card:hover {
    background-color: rgba(213, 1, 255, 0.6);
}


.beer__image:hover{
    opacity: 1;
    cursor: pointer;
}


